@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:700);
@import url(https://fonts.googleapis.com/css?family=Gothic+A1);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,700);
html, body, div, ul, p {
  padding: 0;
  margin: 0;
}

li {
  list-style-type: none;
}

body {
  background-color: #333;
  font-family: 'Gothic A1', 'Open Sans', sans-serif;
}

input, button {
  font-family: 'Gothic A1', 'Open Sans', sans-serif;
}

* {
  box-sizing: border-box;
}
button {
  border: 0; outline: none; cursor: pointer;
}
input[type=submit] {-webkit-appearance:none;}
input[type=type] {
  display: block;
  font-size: 80px;
  max-width: 100%;
  font-weight: 300;
  border: 0;
  border-bottom: 1px solid #ddd;
  outline: none;
  font-family: 'Open Sans', sans-serif;
}

.app-wrap {
  position: absolute;
  display: flex;
  flex-direction: column;
  top: 0; 
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  max-width: 450px;
  max-height: 700px;
  background-color: #fff;
}

.app-wrap .content {
  position: relative;
  height: 100%;
  padding: 40px;
}

.button-wrap {
  position: absolute;
  bottom: 0;
  left: 40px;
  right: 40px;
}

.button-wrap .btn,
.button-wrap input.btn[type="submit"] {
  padding: 1.3em 0 2em;
  width: 100%;
  font-size: 18px;
  font-weight: 600;
  color: white;
  border: none;
  border-radius: 20px 20px 0 0;
  background-color: #1f72d1;
  -webkit-transition: .5s;
  transition: .5s;
  cursor: pointer;
  outline: none;
}

.button-wrap .btn.is-delay {
  background-color: #999;
}

.button-wrap .btn.is-disable {
  background-color: #ddd;
  cursor: none;
  pointer-events: none;
}

@media screen and ( max-width: 767px ) {
  .app-wrap {
    max-width: 100%;
    max-height: 100%;
  }
  .app-wrap .content {
    padding: 15px 40px;
  }
}
.seats-wrap .show-total {
    padding-bottom: 12px;
    border-bottom: 1px solid #e0e0e0;
    margin-bottom: 20px;
}

.seats-wrap .seats-area {
    margin-bottom: 20px;
}

.seats-wrap .seats-area label {
    border: 1px solid #ddd;
    padding: 7px 10px;
    margin-right: 10px;
    color: #999;
}

.seats-wrap .seats-area input[type="radio"] {
    display: none;
}

.seats-wrap .seats-area input[type="radio"]:checked + label {
    color: #1f72d1;
    border-color: #1f72d1;
}

.seats-wrap .seats {
    display: flex;
    margin: 0 -10px;
    flex-direction: row;
}

.seats-wrap .seats .items {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-gap: 5px;
    width: 50%;
    height: 100%;
    padding: 0 10px;
}

.seats-wrap .seats .items.left,
.seats-wrap .seats .items.right {
    grid-template-columns: repeat(3, 1fr);
    width: 25%;
}

.seats-wrap .seats .items .item {
    padding-top: 100%;
    background-color: #eee;
}

.seats-wrap .seats .items .item.seated {
    background-color: #b2b8be;
}

.seats-wrap .seats .items .item.active {
    background-color: #1f72d1;
}

.seats-wrap .show-my-seat {
    position: absolute;
    bottom: 27%;
    left: 0;
    right: 0;
    text-align: center;
    font-size: 50px;
}

.seats-wrap .show-my-seat span {
    color: #1f72d1;
}

.seats-wrap .show-my-seat #jsSeatSide {
    font-size: 30px;
    color: #aaa;
    vertical-align: middle;
}


@media screen and ( max-width: 767px ) {
    .seats-wrap .show-total {
        padding-bottom: 5px;
        margin-bottom: 15px;
    }

    .seats-wrap .show-my-seat {
        bottom: 25%;
    }
}
header {
    background-color: #ffffff;
    padding: 40px;
}

header .header-title {
    display: inline-block;
    padding: 0 0 .15em;
    font-size: 2.3rem;
    font-weight: 600;
    line-height: 1;
    border-bottom: 2px solid #1f72d1;
    font-family: 'Roboto Slab', serif;
}


